import { NavLink } from 'react-router-dom';
import './Footer.scss';
import logo from './../../images/header/logo.png';
import NavigLinkFooter from './reuse/NavigLinkFooter/NavigLinkFooter';

const Footer = () => {
  return (
    <header className='footer'>
      <div className='container'>
        <div className='footer_section'>
          <NavLink to='/'>
            <img className='footer_logo' src={logo} alt='img' />
          </NavLink>
          <NavigLinkFooter
            data={[
              { link: '/about', name: ' ' },
              {
                link: '/press',
                name: '-',
              },
              { link: '/businesses', name: '' },
              { link: '/employees', name: '' },
              { link: '/report', name: ' ' },
              { link: '/contacts', name: '' },
            ]}
          />
        </div>
        <div className='footer_argo'>
            
          <b>
            <a
              className='footer_argo_a'
              target='_blank'
              href='https://argo-soft-works.ru/'
            >
              ArgoSoftWorks
            </a>
          </b>
        </div>
        <div className='footer_allrights'> 2024    </div>
      </div>
    </header>
  );
};
export default Footer;
